/*
  #
  # CUSTOM CLASSES FOR COMMON USE CASES
  #
*/

.bg-contrast-text {
  color: rgb(71, 67, 80);
}

.title-responsive {
  font-size: 5rem;
}

.too-much-sauce {
  font-size: 4rem;
}

.main-header-responsive {
  font-size: 5rem;
