/*
  #
  # DEFAULT CLASSES FOR COMMON USE CASES
  #
*/

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
html {
  font-family: "Montserrat", sans-serif;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  color: #555;
  font-weight: 400;
  width: 100% !important;
  height: 100% !important;
}
h1 {
  color: #333;
  font-size: 64px;
  font-weight: 800;
  margin-top: 0;
  text-transform: uppercase;
  margin-bottom: 10px;
}
h2 {
  margin: 0 0 20px 0;
  font-weight: 700;
  font-size: 42px;
  color: #333;
  text-transform: uppercase;
}
h3 {
  font-size: 18px;
  text-transform: uppercase;
  margin-bottom: 20px;
}
h4 {
  font-size: 14px;
  letter-spacing: 1px;
  margin-bottom: 20px;
  text-transform: uppercase;
}
h5 {
  text-transform: uppercase;
  font-weight: 700;
  line-height: 20px;
}
p {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #888;
}
p.intro {
  margin: 12px 0 0;
  line-height: 24px;
}
a {
  color: #222;
}
a:hover,
a:focus {
  text-decoration: none;
  color: #000;
}
ul,
ol {
  list-style: none;
}
ul,
ol {
  padding: 0;
  webkit-padding: 0;
  moz-padding: 0;
}

.btn:active,
.btn.active {
  background-image: none;
  outline: 0;
  box-shadow: none;
}
a:focus,
.btn:focus,
.btn:active:focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn.active.focus {
  outline: none;
  outline-offset: none;
}
.section-title {
  margin-bottom: 50px;
}
.btn-custom {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  color: #444;
  background-color: transparent;
  border: 2px solid #444;
  padding: 14px 20px;
  margin: 0;
  font-size: 14px;
  border-radius: 0;
  margin-top: 20px;
  margin-right: 30px;
  transition: all 0.5s;
}
.btn-custom:hover,
.btn-custom:focus,
.btn-custom.focus,
.btn-custom:active,
.btn-custom.active {
  color: #eee;
  background-color: #444;
}
/* Header Section */
.intro {
  display: block;
  position: absolute;
  height: auto;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: 0px;
  margin-left: 0px;
  padding: 0;
  background: url(/static/media/headerbanner.2c560c9f.jpg) no-repeat right top;
  background-color: #e5e5e5;
  background-size: cover;
  -o-background-size: cover;
}
.intro .overlay {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,d5dade+100&0.5+0,0+100 */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.5) 0%,
    rgba(213, 218, 222, 0) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80ffffff', endColorstr='#00d5dade', GradientType=1 ); /* IE6-9 */
}
.intro .fa {
  font-size: 94px;
  margin-bottom: 40px;
  color: #fff;
}
.intro p {
  color: #444;
  margin: 10px 0 20px 0;
  font-size: 22px;
}
header .intro-text {
  color: rgba(255, 255, 255, 0.95);
  font-family: customTitleFont;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-text-stroke: 0.5px rgba(255, 255, 255);
}

/* About section */

.about-section {
  background-color: #ffffff;
}

.about-main {
  margin-left: 10%;
  margin-right: 10%;
  display: block;
}

.about-picture {
  display: block;
  width: 50%;
  -webkit-transform: translate(50%);
          transform: translate(50%);
  height: auto;
  padding-top: 0%;
  padding-bottom: 3%;
}

.about-title {
  color: rgb(95, 95, 95);
  font-family: aboutTitleFont;
  font-size: 4vmax;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding-top: 4%;
  padding-bottom: 2%;
}

.project-image {
  width: 35%;
  height: auto;
  padding: 0%;
}

.inline-block-wrapper {
  display: flex;
}

.align-content-left {
  align-content: flex-start;
  width: 50%;
}

.align-content-right {
  align-content: flex-end;
  width: 50%;
}

.project-title {
  padding: 0%;
}

/* Portfolio Section */
#portfolio {
  padding: 40px 0 100px 0;
}
.categories {
  margin-bottom: 80px;
}
ul.cat li {
  display: inline-block;
}
ol.type li {
  display: inline-block;
  margin-right: 20px;
}
ol.type li a {
  font-family: "Montserrat", sans-serif;
  color: #444;
  font-size: 14px;
  padding: 10px 20px;
  border: 2px solid #444;
  border-radius: 0;
  text-transform: uppercase;
}
ol.type li a.active {
  background: #444;
  color: #eee;
}
ol.type li a:hover {
  background: #444;
  color: #eee;
}
.isotope-item {
  z-index: 2;
}
.isotope-hidden.isotope-item {
  z-index: 1;
}
.isotope,
.isotope .isotope-item {
  /* change duration value to whatever you like */
  transition-duration: 0.8s;
}
.isotope-item {
  margin-right: -1px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  padding: 0 10px;
}
.isotope {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition-property: height, width;
}
.isotope .isotope-item {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition-property: opacity, -webkit-transform;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
}
.portfolio-item {
  margin: 10px 0;
  transition: all 0.5s ease-out;
}
.portfolio-item .hover-bg {
  overflow: hidden;
  position: relative;
}
.hover-bg .hover-text {
  position: absolute;
  text-align: center;
  margin: 0 auto;
  color: #333;
  background: rgba(255, 255, 255, 0.75);
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: all 0.5s;
}
.hover-bg .hover-text .overlay-caption {
  display: table;
  height: 100%;
  width: 100%;
}
.hover-bg .hover-text .overlay-caption .overlay-content {
  display: table-cell;
  vertical-align: middle;
}
.hover-bg:hover .hover-text {
  opacity: 1;
}

/* Contact Section */
#contact {
  padding: 60px 0 60px 0;
  background: #262626;
}
#contact h2 {
  font-weight: 400;
  color: #777;
  margin: 60px 0;
}
#contact img {
  width: 140px;
  margin: 5px 30px 10px 0;
}
#contact p {
  color: #666;
  margin-bottom: 20px;
  line-height: 24px;
}
#contact .social {
  margin-top: 40px;
}
#contact .social ul li {
  display: inline-block;
  margin-right: 20px;
}
#contact .social i.fa {
  font-size: 26px;
  padding: 4px;
  color: #777;
  transition: all 0.3s;
}
#contact .social i.fa:hover {
  color: #eee;
}
#contact form {
  padding: 0;
}
#contact .text-danger {
  color: #cc0033;
  text-align: left;
}
label {
  font-size: 12px;
  font-weight: 400;
  float: left;
}
#contact .form-control {
  display: block;
  width: 100%;
  padding: 6px 12px;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.42857143;
  color: #aaa;
  background-color: #444;
  background-image: none;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  transition: none;
}
#contact .form-control:focus {
  border-color: #999;
  outline: 0;
  box-shadow: transparent;
}
.form-control::-webkit-input-placeholder {
  color: #777;
}
.form-control:-moz-placeholder {
  color: #777;
}
.form-control::-moz-placeholder {
  color: #777;
}
.form-control:-ms-input-placeholder {
  color: #777;
}
#contact .btn-default {
  font-weight: 400;
  color: #666;
  background-color: transparent;
  border: 2px solid #666;
  padding: 10px 20px;
  margin: 0;
  font-size: 15px;
  border-radius: 0;
  margin-top: 20px;
  text-transform: uppercase;
  transition: all 0.3s;
}
#contact .btn-default:hover,
.btn-default:focus,
.btn-default.focus,
.btn-default:active,
.btn-default.active {
  background-color: #666;
  color: #999;
}
/* Footer Section*/
#footer {
  background: #262626;
  padding: 50px 0 20px 0;
}
#footer p {
  font-size: 15px;
  color: #666;
}

